.resume {
  position: fixed;
  right: 20px;
  z-index: 1;
}
.resumeBtn {
  padding: 10px;
  border-radius: 10px;
  color: #eaeaea;
  /* background-color: #08d9d6; */
  background-image: linear-gradient(to left, #252a34, #08d9d6);
  font-family: "Lora", serif;

  font-weight: bold;
  cursor: pointer;
  box-shadow: #eaeaea 0px 5px 15px;
  border: 1px solid #252a34;
}
.resumeBtn:hover {
  background-image: linear-gradient(to left, #252a34, #ff2e63);
  color: #eaeaea;
  box-shadow: #252a34 0px 5px 15px;
  border: 1px solid black;
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #08d9d6;
  border-radius: 5px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #252a34;
  border-radius: 5px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #08d9d6;
}

