        left 50% bottom 22%,
      url("https://img.freepik.com/premium-vector/devops-process-laptop-tiny-programmers-practice-development-software-operations_501813-1712.jpg")
        right 5% bottom 7.5%;
    background-size: 250px, 350px, 250px;
    background-repeat: no-repeat;
  }
  .resumeBtn {
